import { lazy } from "react";
import IntroductionContent from "../content/IntroductionContent.json";
import TokenContent from "../content/TokenContent.json";
import DistributionContent from "../content/DistributionContent.json";
import UtilityContent from "../content/UtilityContent.json";

const Container = lazy(() => import("../common/Container"));
const ContentBlock = lazy(() => import("../components/ContentBlock"));

const Tokenomics = () => {
  return (
    <Container>
      <ContentBlock
        type="right"
        title={IntroductionContent.title}
        content={IntroductionContent.text}
        icon={`snakes-on-chain-website/img/svg/introtoken.svg`}
        id="intro"
      />
      <ContentBlock
        type="left"
        title={TokenContent.title}
        content={TokenContent.text}
        icon={`snakes-on-chain-website/img/svg/token.svg`}
        id="token"
      />
      <ContentBlock
        type="right"
        title={DistributionContent.title}
        content={DistributionContent.text}
        icon={`snakes-on-chain-website/img/svg/distribution.svg`}
        id="distribution"
      />
      <ContentBlock
        type="left"
        title={UtilityContent.title}
        content={UtilityContent.text}
        icon={`snakes-on-chain-website/img/svg/utility.svg`}
        id="utility"
      />
    </Container>
  );
};

export default Tokenomics;
